.container{
    display: flex;
    justify-content: center;
    min-width: 500px;
    min-height: 1200px;
}

.container__combat{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

.firstCol__item__container, 
.secondtCol__item__containerb16,
.secondtCol__item__container,
.secondtCol__item__containerb9,
.thirdCol__item__container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.secondtCol__item__container1,
.secondtCol__item__container2,
.secondtCol__item__container3{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.firstCol,
.secondtCol,
.thirdCol{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.firstCol__item,
.secondtCol__item,
.thirdCol__item{
    position: relative;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: rgba(0,0,0,0.3);
    border: 5px solid rgba(0,0,0,0.3);
    display: flex;
    justify-content: center;
    align-items: center;
}

.firstCol__item:hover::after,
.secondtCol__item:hover::after,
.thirdCol__item:hover::after{
    content: "Стоимость навыка: "attr(data-cost) "    Описание: " attr(data-info);
    position: absolute;
    padding: 15px;
    top: 10px;
    left: 60px;
    width: 200px;
    height: 300px;
    border: 1px solid rgba(0,0,0,0.5);
    background-color: white;
    border-radius: 10px;
    z-index: 999;
}

.firstCol__item.active,
.secondtCol__item.active,
.thirdCol__item.active{
    background-color: transparent;
}

.firstCol__item:hover,
.secondtCol__item:hover,
.thirdCol__item:hover{
    background-color: rgba(0,0,0,0.08);
}

.stuff{
    width: 2px;
    height: 30px;
    background-color: rgba(0,0,0,0.5);
    border: 2px solid rgba(0,0,0,0.5);
    border-radius: 5px;
}

.stuff.stuff__active{
    background-color: transparent;
}
.stuff__tree.stuff__active{
    background-color: transparent;
}

.stuff__tree{
    position: relative;
    width: 2px;
    height: 20px;
    background-color: rgba(0,0,0,0.5);
    border: 2px solid rgba(0,0,0,0.5);
    border-radius: 5px;
}
.stuff__tree_right{
    position: absolute;
    top: 10px;
    left: 2px;
    height: 2px;
    width: 205px;
    background-color: rgba(0,0,0,0.5);
    border: 2px solid rgba(0,0,0,0.5);
    border-radius: 5px;
}
.stuff__tree_left{
    position: absolute;
    top: 10px;
    right: 2px;
    height: 2px;
    width: 205px;
    background-color: rgba(0,0,0,0.5);
    border: 2px solid rgba(0,0,0,0.5);
    border-radius: 5px;
}